<script>
  import { RouterLink } from 'vue-router'
  import router from '../router';
  
  export default {
    data() {
      return {
        user: {
          username:"",
          id: "",
          email: ""
        },
        session: {
          active: false,
          id: null,
          username: '',
          email: ''
        }
      }
    },
    created () {
      if (!localStorage.session) {
        localStorage.session = JSON.stringify({
          ...this.session
        })} else {
          this.session = JSON.parse(localStorage.session)
      }
    },
    watch: {
      '$route' (to, from) {
        if(from.name === 'login' && to.name === 'home') {
          this.session = JSON.parse(localStorage.session)
        }
      }
    },
    methods: {
      unregisterUser: async function() {
        const newSession = {
          active: false,
          id: null,
          username: '',
          email: '',
          isAuthoriseAdmin: false,
          isAuthoriseManager: false,
          token: ""
        }
        const response = await fetch(process.env.VUE_APP_API_URL + "/logout", {
              mode: 'cors',
              method: 'DELETE',
              headers: {
                "Content-type": "application/json; charset=UTF-8",
                "Authorization": JSON.parse(localStorage.session).token
              }
        })
        const { status: status, success: success, message: message, error: error } = await response.json()
        if(success) {
          this.$toast.success(message, {position: "top-right"})
        } else {
          this.$toast.error(error, {position: "top-right"})
        }
        localStorage.session = JSON.stringify(newSession)
        this.session = newSession
        router.push('/')

      },
    }
  }
</script>

